<template>
        <div class="box">
          <el-input class="search" id="search" v-model="input" v-on:input="search"   placeholder="请输入内容" ></el-input>
          <div class="searchitem el-input" v-show="show"> 
              <p @click="selectCity(item)"  v-for="(item,index) in list" :key="index">
                {{item.name}}     
            </p>  
        </div>
        
        </div>
         
</template>
<script>
import $ from "jquery"
export default {
    data(){
        return{
            input:"",
            list:[],
            show:false,
        }
    },
    methods:{
        // 模糊搜索
        search(){
            let that=this
            // this.$emit("search")
            var settings = {
                "url": "https://restapi.amap.com/v3/assistant/inputtips?parameters",
                "method": "get",
                "headers": {
                    "Content-Type": "application/json",
                },
                "data": {
                    "key": "4e25e17d46216ebfedf8ab07138da912", //文书编号
                    "keywords":this.input,
                    type:"POI"
                }
                }
                $.ajax(settings).done(function (response) {
                 
                   if(response.tips.length>0){
                       that.show=true
                   }else{
                        that.show=false
                   }
                    that.list= response.tips
                });
        },
        // 获取地址
        selectCity(e){
        this.show=false
           this.input=e.name
           let list
           console.log(e.location)
           if(e.location){
              list =e.location.split(",")
              let one=Number(list[0])
               let two=Number(list[1])
               list=[]
               list.push(one)
                list.push(two)
           }
           else{
               list=null
           }
           console.log(list)
           this.$emit("updetaMaker",JSON.stringify(list))
        }
}
}
</script>
<style lang="">
     .search{
        z-index:20;
        margin-top: 100px;
    }
    .box{
         width: 50%;
         margin-left: 25%;
    }
    .searchitem{
        z-index: 100;
        background: white;

    }
     .searchitem p{
         padding: 10px 0px;
         border-bottom: 1px solid #cccccc;
     }
    .searchitem p:hover{
        background: #aaaaaa;
       
    }
    .has-gutter{
        display: none;
    }
</style>


