<template>
    <div id="container">
      <search  @updetaMaker="setPosition"/>
    </div>
</template>
<script>
import MapLoader from '@/assets/map.js'
import search from "@/components/search.vue"
export default {
    components:{search},
    data(){
        return{
            map:null,
            lng:"",
            lat:"",
            position:[],
            marker:null,
            circle:null,
            input:""
        }
    },
    mounted () {
        this.loadMap()
    },
    methods:{
        // 设置位置图标
        // 加载地图
        loadMap(){
            let that = this
            MapLoader().then(AMap => {
                console.log('地图加载成功')
                that.marker,that.map = new AMap.Map('container', {
                    resizeEnable: true,
                    zoom: 11,
                    // pitch:80,
                    // rotation:-15,
                    // // viewMode:'3D',
                })
                this.getposition()
                this.watchClick()
                
                
                // this.setPosition()
            }, e => {
            console.log('地图加载失败' ,e)
            })
        },
        // 监听事件
        watchClick(){
            let map=this.map
            let that=this
            //   var map=new AMap.Map('container');//重新new出一个对象，传入参数是div的id
            AMap.event.addListener(map,'click',function (e) { //添加点击事件,传入对象名，事件名，回调函数
                 let list=[e.lnglat.lng,e.lnglat.lat]
                 that.setPosition(list)
            })
        },
        // 点击标点
        setPosition(list){
            // console.log(JSON.parse(list))
            let position=this.position
            let pp
            if(list){
                console.log(typeof(list))
                if(typeof(list)=="string"){
                    pp=JSON.parse(list) 
                    this.map.setCenter(pp); 
                }else{
                     pp=list
                }
               
              
            }else{
                pp=[this.lng,this.lat]
            }
             console.log(pp)
            position.push(pp)
        // 点击一个添加一个marker
            // this.position=position
            // for(var a=0;a<position.length;a++){
            //     var marker = new AMap.Marker({
            //     icon: "../../static/position.png",
            //      position: position[a],
            //        draggable: true,
            //          cursor: 'move',
            //     offset: new AMap.Pixel(-20, -20)
            // });
            // }
            // 点击切换marker
            // this.marker=null
           if(this.marker){
               this.map.remove(this.marker)
           }
            this.marker = new AMap.Marker({
                icon: "../../static/position.png",
                     position: pp,
                    draggable: true,
                     cursor: 'move',
                offset: new AMap.Pixel(-20, -40)
            });
                //  this.marker.setMap(this.map);
            this.map.add(this.marker)
            this.setCricle(pp)
        },
        // 设置地图 u 中国心
        select() {
                let map=this.map
                map.setZoom(11);
                map.setCenter([this.lng, this.lat])
        },
        // 获取定位信息
        getposition(){
            let that=this
            let map=this.map
            AMap.plugin('AMap.Geolocation', function() {
                  var geolocation = new AMap.Geolocation({
                enableHighAccuracy: true,//是否使用高精度定位，默认:true
                timeout: 10000,          //超过10秒后停止定位，默认：5s
                buttonPosition:'RB',    //定位按钮的停靠位置
                buttonOffset: new AMap.Pixel(0, 0),//定位按钮与设置的停靠位置的偏移量，默认：Pixel(10, 20)
                zoomToAccuracy: false,   //定位成功后是否自动调整地图视野到定位点

            });
            map.addControl(geolocation);
            geolocation.getCurrentPosition(function(status,result){
                if(status=='complete'){
                    that.onComplete(result)
                }else{
                    that.onError(result)
                }
            });
            });
        },
        showInfoM(e){
            console.log(e)
        },
        //解析定位结果
        onComplete(data) {
            this.lng=data.position.lng
            this.lat=data.position.lat
            this.setPosition()
            
        },
        // 设置定位半径圆
        setCricle(pp){
            if(this.circle){
                    this.map.remove(this.circle)
            }
        this.circle = new AMap.Circle({
            center: new AMap.LngLat(pp[0],pp[1]), // 圆心位置
            radius: 5000,  //半径
            strokeColor: "#F33",  //线颜色
            strokeOpacity: 1,  //线透明度
            strokeWeight: 0,  //线粗细度
            fillColor: "black",  //填充颜色
            fillOpacity: 0.35 //填充透明度
         });
        this.circle.setMap(this.map)
        },
        search(){
            let that=this
            // console.log(that.input)
            AMap.plugin('AMap.Autocomplete', function(){
            // 实例化Autocomplete
            var autoOptions = {
                city:"全国",
                datatype:"all",
                citylimit:false,
                input:"search"
            }
            var autoComplete= new AMap.Autocomplete(autoOptions);
             that.map.addControl(autoComplete);
             autoComplete.search(that.input, (status, result) => {
                 console.log(result)
             })
            // AMap.event.addListener(autoComplete,"select",function(data){
            //     console.log(data)
            // })
        
        })
        }
        
       
    }
}
</script>
<style>
    #container{
        width: 100vw;
        height: 100vh;
    }
    .amap-icon img{
        width: 40px;
        height: 40px;
    }
   
</style>


